/* layout */
:root {
  --primary: #1aac83;
  --error: #e7195a;
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  /* font-family: 'Ibarra Real Nova', serif; */
  /* font-family: 'M PLUS 1p', sans-serif;*/
  font-family: 'Montserrat', sans-serif; 
}

body {
  font-family: Arial, Helvetica, sans-serif;
  background-color: #f4f4f4;
}
.navbar {
    width: 100%;
    height: 110px;

    background-color: #fff;
    opacity: 0.4;
    background-image: linear-gradient(to right, #1C9FA3, #1C9FA3 15px, #fff 5px, #fff );
    background-size: 20px 100%;
    display: flex;
    justify-content: space-between;

    padding: 1.5rem 4rem;
}

.navbar__links {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.navbar__logo h2 {
    color: #fff;
    opacity: 1.0;
    background-color: #1C9FA3;
    border: 1px solid #90C9C2;

    box-shadow: 8px 3px 3px #fff;

    font-weight: bold;
    font-size: 1.4rem;
    
    z-index: 2;
}

.nav_link_item {
	box-shadow: -3px 4px 0px 0px #fff;
	background-color:#1C9FA3;
	border-radius:9px;
	border:1px solid #90C9C2;

	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-family:Arial;
	font-size:17px;
	padding:3px 25px;
	text-decoration:none;
    margin-left: 1em;
}

.nav_link_item:hover {
    /* background:linear-gradient(to bottom, #fff 2%, #1c9fa3 100%); */
	background-color:#b69c7c;
}
.nav_link_item:active {
	position:relative;
	top:1px;
}

.nav_link{
    color: #fff;
    /* text-decoration: none; */
}

.nav_link:hover{
    text-decoration: none;
    color: #fff;
}

@media (max-width: 500px) {
    .navbar__logo h2 {
        font-size: 1rem;
    }
}
.section {
    background: #2d3246;
    padding-top: 1rem
}

.context-dark, .bg-gray-dark, .bg-primary {
    color: rgba(255, 255, 255, 0.8);
}

.footer-classic a, .footer-classic a:focus, .footer-classic a:active {
    color: #ffffff;
}
.nav-list li {
    padding-top: 5px;
    padding-bottom: 5px;
}

.nav-list li a:hover:before {
    margin-left: 0;
    opacity: 1;
    visibility: visible;
}

ul, ol {
    list-style: none;
    padding: 0;
    margin: 0;
}

dd {
    margin-bottom: 0;
}

.social-inner {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    padding: 23px;
    font: 900 13px/1 "Lato", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
    text-transform: uppercase;
    color: rgba(255, 255, 255, 0.5);
}
.social-container .col {
    border: 1px solid rgba(255, 255, 255, 0.1);
}
.nav-list li a:before {
    content: "\F14F";
    font: 400 21px/1 "Material Design Icons";
    color: #4d6de6;
    display: inline-block;
    vertical-align: baseline;
    margin-left: -28px;
    margin-right: 7px;
    opacity: 0;
    visibility: hidden;
    transition: .22s ease;
}
/* .landing__main{
    background-color: #e5ddc3;
} */

.landing__main{
    background-color: #e5ddc3;
}

.landing__text {
    padding: 3em;
}

.hero__img {
    display: flex;
    padding-bottom: 3em;
    justify-content: center;
    align-items: center;
}

.hero__img img {
    width: 50%;
}

#landing_text_title {
    font-family: 'Montserrat', sans-serif;
    font-style: italic;
    font-size: 2rem;
}
main {
  background-color: #36454f;
}


.homepage {
  font-family: 'Montserrat', sans-serif;
  background-color: #e5ddc3;
  max-width: 1300px;
  padding: 1rem;
  color: #171717;
}
.homescreen__subtitle {
  font-size: 1;
}
.homescreen__title {
  font-weight: lighter;
}

.homescreen__services {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}


@media (max-width: 1232px) {
    .homescreen__services {
      grid-template-columns: repeat(2, auto);
    }
}
  
  @media (max-width: 950px) {
    .homescreen__services {
      grid-template-columns: repeat(1, auto);
    }
}
.service__details {
    width: 300px;
    padding: 1rem;
    background: #fff;
    cursor: pointer;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.4);
    margin: 8px auto;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-direction: column;
}

.info__button {
    margin-bottom: 8px;
    display: block;
    width: 100%;
    text-decoration: none;
    text-align: center;
    color: #171717;
    background: #f4f4f4;
    padding: 8px 16px;
    border: 1px solid #171717;
    font-size: 1rem;
}

.info__button:hover {
    background: #171717;
    color: #f4f4f4;
}

.info__name {
    font-size: 1rem;
    overflow: hidden;
}

.info__price {
    font-weight: bold;
}

